import React, { Component } from "react";
import styled from "styled-components";
import { Link } from "react-router-dom";
import { PropTypes } from "prop-types";
import { Button } from "reactstrap";

export default class Product extends Component {
  render() {
    const { id, title, img, price } = this.props.product;

    return (
      <ProductWrapper className="col-9 mx-auto col-md-6 col-lg-3 my-3">
        <div className="card">
          <div
            className="img-container p-3"
            onClick={() => {
              console.log("you clicked me on image container");
            }}
          >
            <Link to={{pathname: '/details', state: this.props.product}}>
              <img src={img} alt="product" className="card-img-top" />
            </Link>
          </div>
          {/*card footer */}
          <div className="card-footer d-flex justify-content-between">
            <span className="align-self-center mb-0">{title}</span>
            <span>&nbsp;&nbsp;</span>
            <h5 className="text-blue font-italic mb-0">
              <span className="mr-1">Rs.</span>
              {price}
            </h5>
          </div>
          <Button
              color="primary"
              // className="cart-btn"
              className="m-3"
              disabled={this.props.inCart}
              onClick={() => {
                if(!this.props.isLoggedIn) {
                  alert("Please login to Add Products");
                  return;
                }
                this.props.onCartAdd({ id, qty: 1 });
                console.log("added to the cart");
              }}
            >
              {this.props.inCart ? (
                <span className="text-capitalize mb-0" disabled>
                  {" "}
                  in cart
                </span>
              ) : (
                <span><i className="fas fa-cart-plus" /> { "Add To Cart"}</span>
              )}
            </Button>
        </div>
      </ProductWrapper>
    );
  }
}

Product.propTypes = {
  product: PropTypes.shape({
    id: PropTypes.number,
    img: PropTypes.string,
    title: PropTypes.string,
    price: PropTypes.number,
    inCart: PropTypes.bool
  }).isRequired
};

const ProductWrapper = styled.div`
  .card {
    border-color: transparent;
    transition: all 500ms linear;
  }
  .card-footer {
    background: transparent;
    border-top: transparent;
    transition: all 500ms linear;
  }
  &:hover {
    .card {
      border: 0.04rem solid rgba(0, 0, 0, 0.2);
      box-shadow: 2px 2px 5px 0 rgba(0, 0, 0, 0.2);
    }
    .card-footer {
      background: rgba(247, 247, 247);
    }
  }
  .img-container {
    position: relative;
    overflow: hidden;
  }
  .card-img-top {
    transition: all 500ms linear;
  }
  .img-container:hover .card-img-top {
    transform: scale(1.2);
  }
  .cart-btn {
    position: absolute;
    bottom: 0;
    right: 0;
    padding: 0.2rem 0.4rem;
    background: var(--lightBlue);
    border: none;
    color: var(--mainWhite);
    font-size: 1.4rem;
    border-radius: 0.5rem 0 0 0;
    transition: all 250ms linear;
    transform: translate(100%, 100%);
  }
  .img-container:hover .cart-btn {
    transform: translate(0, 0);
  }
  .cart-btn:hover {
    color: var(--mainBlue);
    cursor: pointer;
  }
`;
